<template>
  <v-container fill-height class="d-flex justify-center align-center">
    <!-- <v-card width="200px" flat :class="`d-flex justify-center  image_card`">
      <v-img :src="require('../../static/images/纸飞机.png')" width="150px" contain></v-img>
    </v-card>-->
    <v-card flat :class="$vuetify.breakpoint.xsOnly?'login_card-xs':'login_card'">
      <v-text-field label="输入川大学生邮箱" prepend-icon="mdi-email-box" v-model="email" :rules="[filter]">
        <template v-slot:append>
          <v-btn color="primary" small text @click.stop="loginIntroDialog.show=true" class="mb-1">
            <span>邮箱激活指南</span>
          </v-btn>
        </template>
      </v-text-field>
      <div class="d-flex">
        <v-text-field label="输入验证码" prepend-icon="mdi-barcode" v-model="code">
          <template v-slot:append>
            <v-btn
              color="primary"
              small
              text
              class="pb-2"
              @click="getCode"
              v-if="!oneMinuteInterval"
            >点击获取验证码</v-btn>
            <v-btn
              color="primary"
              small
              text
              class="pb-2"
              v-if="oneMinuteInterval"
              disabled
            >{{cnt}}秒后重发</v-btn>
          </template>
        </v-text-field>
        <v-btn color="primary" class="mt-4" icon small @click="loginProblem.show=true">
          <v-icon>mdi-help-circle-outline</v-icon>
        </v-btn>
      </div>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" outlined>
          <span class="text_xsmall" @click="toSCUMail">前往四川大学邮箱</span>
        </v-btn>
        <v-btn color="primary" @click="registerWithEmail">
          <v-icon right>mdi-arrow-right-bold-circle</v-icon>
          <span class="pr-2">登陆</span>
        </v-btn>
      </v-card-actions>
      <div class="d-flex flex-column mt-6 mt-md-8 text_small--- mx-auto" style="width:86%">
        <span class="mx-auto text_small- text_thick">登陆常见问题</span>
        <span class="mx-0">
          <strong>
            概述:
            <br />我们通过给您川大邮箱发送验证码邮件来验证您是否为川大学生，为了限制每个人一个账号，在登陆本app时限制邮箱为:学号+@stu.scu.edu.cn格式。
          </strong>
        </span>
        <li class="mx-0">用户名和密码是什么?</li>
        <span class="ml-5">答: 用户名限制使用学号+@stu.scu.edu.cn方式登陆;密码为激活邮箱时自定义6-18位密码。</span>
        <li class="mx-0">第一次使用邮箱如何激活?</li>
        <span class="ml-5">答: 点击上面“邮箱激活指南”查看</span>
        <li class="mx-0">为什么我收不到邮件?</li>
        <span class="ml-5">答: 请检查邮箱名是否正确，并查看垃圾箱中有无邮件</span>
      </div>
    </v-card>

    <!-- 弹窗 -->
    <v-snackbar v-model="snackbar.show" top :color="snackbar.color">
      {{ snackbar.message }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar.show = false">Close</v-btn>
      </template>
    </v-snackbar>

    <v-dialog
      v-model="loginIntroDialog.show"
      scrollable
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <v-icon>mdi-login</v-icon>
          <span class="ml-1">邮箱激活指南</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-1">
          <v-carousel cycle height="400" hide-delimiter-background show-arrows-on-hover>
            <v-carousel-item v-for="(item, i) in loginIntroDialog.intro" :key="i">
              <v-img
                :src="require(`../../static/images/loginIntro/${i+1}.png`)"
                contain
                max-height="300px"
              ></v-img>
              <div v-html="item"></div>
            </v-carousel-item>
          </v-carousel>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="loginProblem.show"
      scrollable
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <v-icon>mdi-help-circle-outline</v-icon>
          <span class="ml-2">登陆常见问题</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-1">
          <div>
            <li>
              <strong>Q：收不到验证码怎么办？</strong>
            </li>
            <span>A：请检查邮箱名是否输入正确，如果正确，请多刷新几次，检查邮件垃圾箱中是否有验证码。</span>
            <li>
              <strong>Q：邮箱无法登陆？</strong>
            </li>
            <span>A：请确认四川大学邮箱是否激活了，如果没有激活，请按照“邮箱输入栏”中的“邮箱激活指南”步骤激活邮箱</span>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="secretDialog"
      scrollable
      fullscreen
      persistent
      max-width="500px"
      transition="dialog-bottom-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <span>隐私协议</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <p class="mt-3">欢迎来到『火锅底料』。</p>
          <p>
            请您仔细阅读以下条款,若干您对本协议的任何条款表示异议,您可以选择不进入『火锅底料』。当您注册成功，无论是进入
            『火锅底料』，还是在『火锅底料』上发布任何内容（即『内容』），均意味着您（即『用户』）完全接受本协议项下的全部条款
          </p>
          <li>您可以使用四川大学邮箱登陆『火锅底料』，并将其作为您的用户账号，用户应当对其用户账号进行的所有活动和事件负法律责任</li>
          <li>
            用户须对在『火锅底料』上的注册信息的真实性、合法性、有效性承担全部责任，用户不得冒充他人；不得恶意使用注册账号导致其他用户误认
            否则『火锅底料』有权立即停止提供服务，收回其账号并由用户独自承担由此而产生的一切法律责任
          </li>
          <li>
            用户直接或通过各类方式间接使用『火锅底料』服务和数据的行为，都将被视作已无条件接受本协议全部内容；若用户对本协议的任何条款
            存在任何异议，请停止使用『火锅底料』所提供的全部服务
          </li>
          <li>用户承诺不得以任何方式利用『火锅底料』直接或间接从事违反中国法律的行为，『火锅底料』有权对违反上述承诺的内容予以删除</li>
          <li>用户不得利用『火锅底料』服务制作、上载、复制、发布、传播或者转载如下内容：</li>
          <ul>
            <li type="circle">反对宪法所确定的基本原则的；</li>
            <li type="circle">危害国家安全，泄露国家秘密，颠覆国家政权，破坏国家同一的；</li>
            <li type="circle">损害国家荣誉和利益的；</li>
            <li type="circle">煽动民族仇恨、民族歧视、破坏民族团结的；</li>
            <li type="circle">侮辱、滥用英烈形象、否定英烈事迹、美化粉饰侵略战争行为的；</li>
            <li type="circle">破坏国家宗教政策，宣扬邪教和封建迷信的；</li>
            <li type="circle">散步谣言，扰乱社会秩序，破坏社会稳定的；</li>
            <li type="circle">散布淫秽、色情、赌博、暴力、凶杀、恐怖或者教唆犯罪的；</li>
            <li type="circle">侮辱或者诽谤他人，侵害他人合法权益的；</li>
            <li type="circle">含有法律、行政法规禁止的其他内容的信息。</li>
          </ul>
          <br />
          <p>
            『火锅底料』有权对用户使用『火锅底料』的情况进行审查和监督。如果用户在使用『火锅底料』时违反上述任何规定
            ，『火锅底料』或其授权的人有权要求用户改正或直接采取一切必要的措施（包括但不限于更改或者删除用户张贴的内容、暂停
            或终止用户使用『火锅底料』的权利），以减轻用户不当行为造成的影响。当然，在大多数情况下，我们避免对用户所发布内容进行任何意义上的干预。
          </p>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="float-right" color="primary" @click="secretDialogConfirm(true)">同意</v-btn>
          <v-btn
            class="float-right"
            color="primary"
            outlined
            @click="secretDialogConfirm(false)"
          >不同意</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
// let timeout = setTimeout(() => {
//   this.snackbar.show = false;
// }, 1000);
import qs from "qs";
import isNull from '@/utils/isNull'
export default {
  name: "login",
  data() {
    return {
      clientHeight: document.documentElement.clientHeight,
      loginIntroDialog: {
        show: false,
        intro: [
          ' <div class="mt-1"  >  <li >用户名默认为学号或者姓名全拼</li> <li>密码为自定义的6-18位密码</li> <li><strong class="">第一次使用邮箱需要激活，激活步骤请继续看下一张卡片</strong></li></div>'
          , '    <div class="mt-1"> <li>学校选择<strong>四川大学学生邮箱</strong></li> <li>默认密码为stu+身份证后六位</li> </div>',
          ' <div class="mt-3"> <li>请绑定常用手机</li> </div>',
          '   <div class="mt-3"> <li>设置密码后点击激活即可</li> </div>'
        ],
        introImages: ['../../static/images/loginIntro/1.png', '../../static/images/loginIntro/2.png', '../../static/images/loginIntro/3.png', '../../static/images/loginIntro/4.png']
      },
      loginProblem: {
        show: false,
      },
      ipProblemDialog: false,
      email: "@stu.scu.edu.cn",
      code: "",
      oneMinuteInterval: false,
      cnt: 20,
      snackbar: {
        show: false,
        message: "",
        color: "",
      },
      filter: v => {
        var pattern = /^([0-9_\-\.\u4e00-\u9fa5])+@stu.scu.edu.cn/;
        return (pattern.test(v) || this.whiteList.includes(v)) ? true : '邮箱不合法'
      },
      whiteList: [],
      secretDialog: false
    };
  },
  created() {
    let that = this
    that.$request('get', '/user/getWhiteList', {}, {})
      .then(res => { that.whiteList = res.data.map(item => item.email) })
    setTimeout(() => {
      that.secretDialog = true;
    }, 600);
  },
  mounted() {
    window.onresize = () => {
      this.clientHeight = document.documentElement.clientHeight;
    };
  },
  methods: {

    registerWithEmail() {
      let that = this;
      if (isNull(this.code)) { that.showSnackbar('验证码不能为空'); return; }
      that.$request('post', '/user/registerWithEmail', {}, {
        email: that.email,
        code: that.code,
      })
        .then((res) => {
          that.showSnackbar(res, 'ok')
          localStorage.setItem("token", res.data);
          setTimeout(async () => {
            that.$router.replace("/main/index")
          }, 1000);

        })
        .catch(e => {
          that.showSnackbar(e)
        })
    },
    getCode() {
      let that = this;
      if (this.filter(this.email) == '邮箱不合法') {
        that.showSnackbar('邮箱不合法')
        return;
      }
      that.$request('post', '/user/getCode', {}, { email: this.email })
        .then(res => {
          that.showSnackbar(res || '验证码已发送到邮箱,请查收', 'ok')
        })
        .catch(e => {
          that.showSnackbar(e)
        })
      this.oneMinuteInterval = true;
      let interval = setInterval(() => {
        this.cnt--;
        if (this.cnt == 0) {
          this.oneMinuteInterval = false;
          clearInterval(interval);
          this.cnt = 20
        }
      }, 1000);
    },

    toSCUMail() {
      window.open('http://mail.stu.scu.edu.cn/coremail/index.jsp?nodetect=true', '_system', {})
    },
    secretDialogConfirm(v) {
      if (v) {
        this.secretDialog = false
      }
      else {
        this.showSnackbar('感谢使用,即将退出程序','ok')
        setTimeout(() => {
          navigator.app.exitApp()
        }, 1800);
      }
    }

  },
  watch: {
    "ipProblemDialog"(v) {
      this.$store.change_dialog(v, 'ipProblemDialog')
    },
    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'ipProblemDialog')
        this.ipProblemDialog = v
    }


  },
};
</script>

<style>
.image_card {
  margin-top: -120px;
}
.login_card {
}
.login_card-xs {
  width: 95%;
  margin-top: 46%;
}
</style>



